import React, { Component, lazy, Suspense } from "react";
import { connect } from "react-redux";
import { getsupplierList } from "../actions/supplierAction";
import { getUUID } from "../actions/fakturAction";
import { reset } from "redux-form";
import Swal from "sweetalert2";
import { getTerimaSupplierTemp } from "../actions/terimaSupplierAction";
import { warning } from "../Alert";
import PrintNota from "../Stocking/Component/PrintNota";
// import { Link } from "react-router-dom";
import {
  finishSend,
  getBruto,
  getNetto,
  getPkg,
  getQty,
  getRp,
  progressSend,
} from "../actions/LogicAction";
import { deleteIndexUpdatetotalNW } from "../deleteLocalStorage";
import ReactTooltip from "react-tooltip";
import SelectSearch from "react-select-search";
import Skeleton from "react-loading-skeleton";
import { showModal } from "../actions/ModalAction";
import { AxiosMasterGet, AxiosMasterPost } from "../Axios";
import HeaderCard from "../HeaderCard";
import GlobalTabel from "../GlobalComponent/GlobalTabel";
import FormBarcodePenerimaanSupplier from "./Component/FormBarcodePenerimaanSupplier";

const FormTerimaSupplier = lazy(() => import("./Component/FormTerimaSupplier"));
const ModalMasterData = lazy(() =>
  import("../DataMaster/Component/ModalMasterData")
);

const maptostate = (state) => {
  return {
    supplierList: state.provinsi.supplier,
    tampungBarang: state.provinsi.terimaSupplierTemp,
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    totalNW: state.provinsi.totalNW,
    sumBruto: state.perhitungan.sumBruto,
    sumNetto: state.perhitungan.sumNetto,
    sumRp: state.perhitungan.sumRp,
    sumPkg: state.perhitungan.sumPkg,
    sumQty: state.perhitungan.sumQty,
    onSend: state.provinsi.onSend,
  };
};

class TerimaSupplier extends Component {
  constructor(props) {
    super(props);
    var date = new Date();
    var formatedDate = `${date.getFullYear()}-${
      date.getMonth() + 1
    }-${date.getDate()}`;
    this.state = {
      listSupplier: [],
      onDelete: false,
      columns: [
        {
          dataField: "kode_jenis",
          text: "Kode Barang",
        },
        {
          dataField: "kadar_cetak",
          text: "Kadar Cetak(%)",
        },
        {
          dataField: "plastik",
          text: "P",
        },
        {
          dataField: "kulit",
          text: "K",
        },
        {
          dataField: "pkg",
          text: "Bungkus(Gr)",
        },
        {
          dataField: "bruto",
          text: "Berat(Gr)",
        },
        {
          dataField: "kadar_beli",
          text: "Harga(%)",
        },
        {
          dataField: "netto",
          text: "Murni(Gr)",
          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "ongkos_rp",
          text: "Ongkos(RP)",
          formatter: (cell) => (
            <span>Rp. {parseFloat(cell).toLocaleString("ID-id")}</span>
          ),
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger mr-2"
                  onClick={() =>
                    deleteIndexUpdatetotalNW(
                      "TerimaSupplier",
                      rowIndex,
                      this.props.dispatch,
                      getTerimaSupplierTemp()
                    )
                      .then(() => {
                        var barang =
                          JSON.parse(localStorage.getItem("TerimaSupplier")) ||
                          [];
                        localStorage.setItem(
                          "totalNW",
                          barang
                            .map((list) => parseFloat(list.netto))
                            .reduce((a, b) => a + b, 0)
                            .toFixed(3)
                        );
                      })
                      .then(() =>
                        this.setState({
                          totalNW: localStorage.getItem("totalNW") || 0,
                        })
                      )
                  }
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash mr-1"></i>
                </button>
                <br />
                {/* <button
                  data-tip
                  data-for="edit"
                  className="btn btn-warning mr-2"
                  data-toggle="modal"
                  data-target="#editModal"
                  onClick={() => this.setEdit(row)}
                >
                  <ReactTooltip id="edit" type="dark" effect="solid">
                    <span>Edit</span>
                  </ReactTooltip>
                  <i className="fa fa-edit mr-1"></i>
                </button> */}
              </div>
            );
          },
        },
      ],
      tanggal_invoice: formatedDate,
      tanggal_barang: formatedDate,
      noBon: "",
      totalNW: localStorage.getItem("totalNW") || 0,
      totalBruto: 0,
      totalPkg: 0,
      totalGross: 0,
      totalOngkir: 0,
      kodeSupplier: "",
      namaSupplier: "",
      keterangan: "",
      startingDate: formatedDate,
      tampungBarang: [],
      tampungBarangLocal: [],
      urutan: localStorage.getItem("urutan") || 0,
    };
  }

  componentDidMount() {
    this.props.dispatch(getsupplierList());
    AxiosMasterGet("supplier/get/all")
      .then((res) =>
        this.setState({
          listSupplier: res.data,
        })
      )
      .catch((err) => alert(err));
    this.props.dispatch(getUUID());
    this.props.dispatch(getTerimaSupplierTemp());
    this.getSumData();
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
    localStorage.setItem([input], e.target.value);
  };

  pilihSupplier(data) {
    var supp = data ? data : "DEFALT | DEFAULT";
    var result_explode = supp.split("|");
    this.setState({
      kodeSupplier: result_explode[0],
      namaSupplier: result_explode[1],
    });
    localStorage.setItem("namaSupplier", result_explode[0]);
    localStorage.setItem("kodeSupplier", result_explode[1]);
  }

  handleSubmit(data) {
    var barang = JSON.parse(localStorage.getItem("TerimaSupplier")) || [];
    var jenis = data.kodeJenis ? data.kodeJenis : "DEFALT | DEFAULT";
    var result_explode = jenis.split("|");

    let isi = {
      no_urut: barang.length + 1,
      kode_jenis: result_explode[0],
      keterangan: data.keterangan.toUpperCase(),
      kadar_cetak: result_explode[2],
      kadar_beli: parseFloat(data.kadarTransaksi),
      kode_barcode: "-",
      qty: 1,
      plastik: parseFloat(data.p),
      kulit: parseFloat(data.k),
      pkg: parseFloat(data.pkg),
      gross: parseFloat(data.gross),
      bruto: parseFloat(data.bruto),
      netto: parseFloat(data.netto),
      ongkos_rp: parseFloat(data.rp || 0),
    };
    barang.push(isi);
    localStorage.setItem("TerimaSupplier", JSON.stringify(barang));
    localStorage.setItem(
      "totalNW",
      barang
        .map((list) => list.netto)
        .reduce((a, b) => a + b, 0)
        .toFixed(3)
    );
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Transaksi Berhasil",
      showConfirmButton: false,
      timer: 1500,
    })
      .then(() => this.props.dispatch(reset("TerimaSupplier")))
      .then(() => this.getSumData())
      .then(() => this.props.dispatch(getTerimaSupplierTemp()));
  }
  getSumData() {
    this.props.dispatch(getNetto("TerimaSupplier"));
    this.props.dispatch(getBruto("TerimaSupplier"));
    this.props.dispatch(getRp("TerimaSupplier"));
    this.props.dispatch(getPkg("TerimaSupplier"));
    this.props.dispatch(getQty("TerimaSupplier"));
  }
  kirimData() {
    var jenis = this.state.kodeSupplier
      ? this.state.kodeSupplier
      : "DEFALT | DEFAULT";
    var result_explode = jenis.split("|");
    if (this.state.kodeSupplier === "") {
      warning("Mohon pilih Supplier");
      return false;
    } else if (this.state.keterangan === "") {
      warning("Mohon isi Keterangan");
      return false;
    } else if (this.state.noBon === "") {
      warning("Mohon Isi nomor Bon");
      return false;
    } else if (localStorage.getItem("TerimaSupplier") === null) {
      warning("Barang masih kosong, mohon tambahkan barang");
      return false;
    } else if (this.state.tanggalBarang === "") {
      warning("Mohon isi Tanggal");
      return false;
    } else if (this.state.tanggal === "") {
      warning("Mohon isi Tanggal Invoice");
      return false;
    }
    this.props.dispatch(progressSend());
    var simpan = {
      //
      no_terima: this.props.noFaktur.toUpperCase(),
      no_bon: this.props.noFaktur.toUpperCase(),
      kode_supplier: result_explode[0],
      total_netto: this.props.sumNetto,
      total_netto_ext: this.props.totalNW,
      tanggal_invoice: this.state.tanggal,
      tanggal_barang: this.state.tanggalBarang,
      detail_barang: JSON.parse(localStorage.getItem("TerimaSupplier")),
    };

    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = JSON.parse(localStorage.getItem("TerimaSupplier"));
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        ++i,
        ticket.kode_jenis,
        ticket.kadar_cetak,
        ticket.plastik,
        ticket.kulit,
        ticket.pkg,
        ticket.bruto,
        ticket.kadar_beli,
        ticket.netto,
        parseFloat(ticket.ongkos_rp).toLocaleString("id-ID"),
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    const footer = [
      "",
      "",
      "",
      "",
      "",
      table.map((list) => parseFloat(list.pkg)).reduce((a, b) => a + b, 0),
      table.map((list) => parseFloat(list.bruto)).reduce((a, b) => a + b, 0),
      "",
      table
        .map((list) => parseFloat(list.netto))
        .reduce((a, b) => a + b, 0)
        .toFixed(3),
      table
        .map((list) => parseInt(list.ongkos_rp))
        .reduce((a, b) => a + b, 0)
        .toLocaleString("id-ID"),
    ];
    tableRows.push(footer);
    console.log(JSON.stringify(tableRows));
    var columTable = [
      "No",
      "Kode Barang",
      "Kadar Cetak (%)",
      "P",
      "K",
      "Bungkus(Gr)",
      "Berat(Gr)",
      "Harga(Gr)",
      "Murni(Gr)",
      "Ongkos(Rp)",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    AxiosMasterPost("trx-supplier/terima-barang", simpan)
      .then((res) =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => this.props.dispatch(getTerimaSupplierTemp()))
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() =>
            PrintNota(
              "no Terima",
              this.props.noFaktur.toUpperCase(),
              "Tanggal",
              this.state.tanggal_invoice,
              "No Bon",
              this.state.noBon.toUpperCase(),
              "Supplier",
              this.state.kodeSupplier.toUpperCase(),
              "ADMIN",
              this.state.tanggal,
              "",
              columTable,
              "Terima Barang Supplier",
              tableRows
            )
          )
          .then(() => localStorage.removeItem("TerimaSupplier"))
          .then(() => localStorage.removeItem("noBon"))
          .then(() => localStorage.removeItem("tanggal"))
          .then(() => localStorage.removeItem("tanggalBarang"))
          .then(() => localStorage.removeItem("namaSupplier"))
          .then(() => localStorage.removeItem("kodeSupplier"))
          .then(() => localStorage.removeItem("keterangan"))
          .then(() => localStorage.removeItem("totalNW"))
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() => localStorage.setItem("isLogin", true))
          .then(() => this.props.dispatch(getUUID()))
          .then(() =>
            this.setState({
              noBon: "",
              keterangan: "",
              totalNW: "",
              kodeSupplier: "",
            })
          )
          .then(() => this.props.dispatch(getTerimaSupplierTemp()))
          .then(() => document.getElementById("myForm").reset())
          .then(() => this.props.dispatch(finishSend()))
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Ditambahkan",
          showConfirmButton: false,
          timer: 1500,
        }).then(() => this.props.dispatch(finishSend()))
      );
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Terima Barang Supplier" />
              <div className="card-body">
                <div className="col-lg-12 col-md-12">
                  <div className="row">
                    <div className="col-lg-9"></div>
                    <div className="col-lg-12">
                      <br />
                    </div>
                    <div className="col-lg-12">
                      <form id="myForm">
                        <div className="row">
                          <div className="col-lg-4 col-md-4">
                            <label htmlFor="">Supplier</label>

                            {this.state.listSupplier ? (
                              <SelectSearch
                                name="supplier"
                                id="kodeSupplier"
                                options={this.state.listSupplier.map((kode) => {
                                  let data = {
                                    value:
                                      kode.kode_supplier +
                                      "|" +
                                      kode.nama_supplier,
                                    name: kode.nama_supplier,
                                  };
                                  return data;
                                })}
                                search
                                placeholder="Silahkan Pilih Supplier"
                                onChange={(data) => this.pilihSupplier(data)}
                              />
                            ) : (
                              <Skeleton width={"100%"} height={50} />
                            )}
                          </div>
                          <div className="col-md-4 col-lg-4">
                            <label htmlFor="">Tanggal Invoice</label>
                            <input
                              type="date"
                              id="tanggal_invoice"
                              name="tanggal_invoice"
                              className="form-control"
                              onChange={this.inputChange("tanggal")}
                            />
                          </div>
                          <div className="col-md-4 col-lg-4">
                            <label htmlFor="">Tanggal Barang</label>
                            <input
                              type="date"
                              id="tanggal_barang"
                              name="tanggal_barang"
                              className="form-control"
                              onChange={this.inputChange("tanggalBarang")}
                            />
                          </div>
                          <div className="col-md-6 col-lg-4 mt-3">
                            <label htmlFor="">Keterangan</label>
                            <input
                              type="text"
                              id="keterangan"
                              name="keterangan"
                              className="form-control"
                              onChange={this.inputChange("keterangan")}
                              value={localStorage.getItem("keterangan") || ""}
                            />
                          </div>
                          <div className="col-md-4 col-lg-4 mt-3">
                            <label htmlFor="">No Bon</label>
                            <input
                              type="text"
                              id="noBon"
                              name="noBon"
                              className="form-control"
                              onChange={this.inputChange("noBon")}
                              value={localStorage.getItem("noBon") || ""}
                            />
                          </div>
                          <div className="col-md-4 col-lg-4 mt-3">
                            <label htmlFor="">Total NW Ext</label>
                            <input
                              type="number"
                              step={0.001}
                              min={0}
                              id="totalNW"
                              name="totalNW"
                              className="form-control"
                              onChange={this.inputChange("totalNW")}
                              value={this.props.totalNW || 0}
                            />
                          </div>
                          <ModalMasterData
                            content={
                              <Suspense
                                fallback={
                                  <Skeleton width={"100%"} height={400} />
                                }
                              >
                                <FormTerimaSupplier
                                  from="terima"
                                  onSubmit={(data) => this.handleSubmit(data)}
                                  noFaktur={this.props.noFaktur}
                                />
                              </Suspense>
                            }
                            title="Tambah Data Barang"
                          />

                          <div className="col-md-12 col-lg-12 mt-3">
                            <div className="row">
                              <div className="col-lg-4"></div>
                              <div className="col-lg-4 col-md-4 mb-3">
                                <div className="text-center">
                                  <button
                                    className="btn btn-primary"
                                    data-toggle="modal"
                                    type="button"
                                    data-target="#barcodeModal"
                                  >
                                    BARCODE (+)
                                  </button>
                                  <button
                                    className="btn btn-primary ml-2"
                                    data-toggle="modal"
                                    disabled
                                  >
                                    BARCODE (-)
                                  </button>
                                </div>
                              </div>
                              <div className="col-lg-4 col-md-6">
                                <div className="text-right">
                                  <button
                                    className="btn btn-primary"
                                    type="button"
                                    onClick={() =>
                                      this.props.dispatch(showModal())
                                    }
                                  >
                                    Tambah Data
                                    <i className="fa fa-plus ml-3"></i>
                                  </button>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                      </form>
                    </div>
                  </div>
                  <GlobalTabel
                    keyField="kodeJenis"
                    data={this.props.tampungBarang || []}
                    columns={this.state.columns}
                    textEmpty="Silahkan Tambah Data Dengan Klik Tombol Biru Diatas"
                  />

                  <div className="col-lg-12 col-md-12">
                    <div className="text-right">
                      <button
                        type="button"
                        className="btn btn-primary"
                        onClick={() => this.kirimData()}
                        disabled={this.props.onSend}
                      >
                        {this.props.onSend ? (
                          <span
                            className="spinner-border spinner-border-sm mr-3"
                            aria-hidden="true"
                          ></span>
                        ) : null}
                        Simpan
                        <i className="fa fa-paper-plane ml-3"></i>
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="barcodeModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-xl">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Data Barcode
                </h4>
                <button
                  type="button"
                  id="dismissModal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  Ã—
                </button>
              </div>
              <div className="modal-body">
                <FormBarcodePenerimaanSupplier />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(TerimaSupplier);
